
* {
  box-sizing: border-box;
}

.section-skills {
  display: flex;
  margin: auto;
  width: 70%;
}

.skills-title {
  font-family: 'Roboto', sans-serif;
	font-size: 30px;
	color: #34495e;
	font-weight: bolder;
}
.skills-hr {
  border: none;
  height: 2px;
  max-width: 60px;
  margin: 10px 0;
  background: #bdc3c7;
}

.icon-skill {
  width: 60px;
  margin: 20px;
  display: inline-block;
}
.react_{
  fill: skyblue !important; 
}

 


/* Responsive para celular */
@media (max-width:700px) {
	.section-skills{
		display: flex;
    width: 86%;
  }
  .skills-title{
		margin: 0;
		display: flex;
    margin-left: 1rem;
	}
	.skills-hr{
		margin-left: 1rem;
		
  }
  .icon-skill {
    width: 46px;
    margin: 3px;
    margin-left: 1rem;
  }

}

/* CSS hvr-buzz */

.hvr-grow {
  display: inline-block;
  vertical-align: middle;
  -webkit-transform: perspective(1px) translateZ(0);
  transform: perspective(1px) translateZ(0);
  box-shadow: 0 0 1px rgba(0, 0, 0, 0);
  -webkit-transition-duration: 0.3s;
  transition-duration: 0.3s;
  -webkit-transition-property: transform;
  transition-property: transform
}
.hvr-grow:active,
.hvr-grow:focus,
.hvr-grow:hover {
  -webkit-transform: scale(1.1);
  transform: scale(1.1)
}
.hvr-shrink {
  display: inline-block;
  vertical-align: middle;
  -webkit-transform: perspective(1px) translateZ(0);
  transform: perspective(1px) translateZ(0);
  box-shadow: 0 0 1px rgba(0, 0, 0, 0);
  -webkit-transition-duration: 0.3s;
  transition-duration: 0.3s;
  -webkit-transition-property: transform;
  transition-property: transform
}
.hvr-shrink:active,
.hvr-shrink:focus,
.hvr-shrink:hover {
  -webkit-transform: scale(0.9);
  transform: scale(0.9)
}
@-webkit-keyframes hvr-pulse {
  25% {
      -webkit-transform: scale(1.1);
      transform: scale(1.1)
  }
  75% {
      -webkit-transform: scale(0.9);
      transform: scale(0.9)
  }
}
@keyframes hvr-pulse {
